import { create } from "zustand";

type ViewType =
  | "register"
  | "login"
  | "dashboard"
  | "reset-password"
  | "verify-email"
  | "update-profile"
  | "delete-account";

interface AuthState {
  view: ViewType;
  setView: (view: ViewType) => void;
}

export const useAuthStore = create<AuthState>((set)=>({
    view: "register",
    setView: (newView) => set({ view: newView })
}))
